.nav-menu{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;

}


.nav-menu-ul{
    display: flex;
}
.nav-menu-ul li{
    padding: 0 1rem;
}
.nav-menu-ul li a{
    font-size: 1.2rem;
    font-weight: 500;
}
.hemburger{
    display: none;
}

@media screen and (max-width:1040px) {
    .nav-menu-ul{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgba(0,0,0,0.9)
        ;
     position: absolute;
      top: 0;
       left: -100%;
        z-index: -3;
        transition: 0.3s;

    }

        .nav-menu-ul.active{
            left: 0;
        }

        .nav-menu-ul li{
            padding: 1rem 0;

        }
                .nav-menu-ul li a{
                    font-size: 2rem;

                }
.hemburger {
    display:initial;
}
}